.services-container {
  width: 100%;
  display: flex;
  overflow: auto;
  min-height: 100vh;
  align-items: center;
  flex-direction: column;
  justify-content: flex-start;
  background-image: linear-gradient(180deg, rgb(0, 0, 0) 28.00%,rgb(255, 255, 255) 100.00%);
}
.services-header {
  flex: 0 0 auto;
  width: 100%;
  display: flex;
  padding: var(--dl-space-space-doubleunit);
  z-index: 100;
  position: relative;
  max-width: 1110px;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
}
.services-logo {
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
}
.services-image {
  width: 100px;
  object-fit: cover;
}
.services-text {
  color: #58f150;
}
.services-menu {
  display: flex;
  align-items: flex-start;
  flex-direction: row;
}
.services-navlink {
  color: #ffffff;
  transition: 0.3s;
  margin-right: var(--dl-space-space-doubleunit);
  text-decoration: none;
}
.services-navlink:hover {
  color: var(--dl-color-turquoise-default);
}
.services-navlink1 {
  color: #14f10f;
  transition: 0.3s;
  margin-right: var(--dl-space-space-doubleunit);
  text-decoration: none;
}
.services-navlink1:hover {
  color: var(--dl-color-turquoise-default);
}
.services-navlink2 {
  color: #ffffff;
  transition: 0.3s;
  margin-right: var(--dl-space-space-doubleunit);
  text-decoration: none;
}
.services-navlink2:hover {
  color: var(--dl-color-turquoise-default);
}
.services-text01 {
  color: #ffffff;
  transition: 0.3s;
  margin-right: var(--dl-space-space-doubleunit);
}
.services-text01:hover {
  color: var(--dl-color-turquoise-default);
}
.services-text02 {
  color: #ffffff;
  transition: 0.3s;
  margin-right: var(--dl-space-space-doubleunit);
}
.services-text02:hover {
  color: var(--dl-color-turquoise-default);
}
.services-text03 {
  color: #ffffff;
  transition: 0.3s;
}
.services-text03:hover {
  color: var(--dl-color-turquoise-default);
}
.services-icon {
  fill: #D9D9D9;
  width: 24px;
  height: 24px;
}
.services-container1 {
  border: 2px dashed rgba(120, 120, 120, 0.4);
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
  background-color: #000000;
}
.services-burger-menu {
  display: none;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
}
.services-menu1 {
  width: 24px;
  height: 24px;
  margin-left: var(--dl-space-space-unit);
}
.services-features {
  flex: 0 0 auto;
  width: 100%;
  display: flex;
  padding: var(--dl-space-space-doubleunit);
  z-index: 100;
  max-width: 1110px;
  align-items: center;
