@media(max-width: 991px) {
  .services-container1 {
    display: none;
  }
  .services-achievements {
    flex-direction: column;
  }
  .services-container3 {
    margin-top: var(--dl-space-space-doubleunit);
  }
}
@media(max-width: 767px) {
  .services-menu {
    display: none;
  }
  .services-burger-menu {
    display: block;
  }
  .services-text04 {
    font-size: 40px;
  }
  .services-text07 {
    font-size: 40px;
  }
}
@media(max-width: 479px) {
  .services-container1 {
    display: none;
  }
  .services-text04 {
    text-align: center;
  }
  .services-text05 {
    text-align: center;
  }
  .services-container2 {
    align-items: center;
  }
  .services-text07 {
    text-align: center;
  }
  .services-text11 {
    text-align: center;
  }
  .services-container4 {
    flex-direction: column;
  }
  .services-container5 {
    flex-direction: column;
  }
}
